:root {
  --color-text: #fff;
  --color-background: #000000d9;
  --font-family: "Consolas", monospace;
  --font-size-base: 14px;
  --font-size-small: 12px;
  --font-size-large: 16px;
}

body, html {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

main {
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
}

#terminal-container {
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
}

.terminal {
  background-color: var(--color-background);
  border: 2px solid var(--color-text);
  width: 100%;
  max-width: none;
  height: 100%;
  min-height: 400px;
  font-family: var(--font-family);
  font-size: var(--font-size-base);
  backdrop-filter: blur(5px);
  box-sizing: border-box;
  border-radius: 5px;
  margin: 0;
  padding: 20px;
  position: relative;
}

.terminal-header {
  border-bottom: 1px solid var(--color-text);
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  padding-bottom: 10px;
  display: flex;
}

.terminal-buttons {
  gap: 8px;
  display: flex;
}

.terminal-button {
  border: none;
  border-radius: 50%;
  width: 12px;
  height: 12px;
}

.terminal-button.close {
  background-color: #ff5f56;
}

.terminal-button.minimize {
  background-color: #ffbd2e;
}

.terminal-button.maximize {
  background-color: #27c93f;
}

.terminal-title {
  color: #fff;
  font-size: var(--font-size-base);
  font-weight: bold;
}

.terminal-content {
  height: calc(100% - 50px);
  padding: 10px 0;
  overflow-y: auto;
}

.command-line {
  margin-bottom: 10px;
  display: flex;
}

.prompt {
  color: #0f0;
  margin-right: 10px;
}

.command-input {
  color: var(--color-text);
  font-family: var(--font-family);
  font-size: var(--font-size-base);
  background: none;
  border: none;
  outline: none;
  width: 100%;
}

.command-input:disabled {
  opacity: 1;
  color: var(--color-text);
  cursor: default;
  -webkit-text-fill-color: var(--color-text);
}

.command-output {
  margin-bottom: 15px;
  margin-left: 20px;
  line-height: 1.4;
}

.cursor {
  background-color: var(--color-text);
  width: 8px;
  height: 15px;
  animation: 1s infinite blink;
  display: inline-block;
}

@keyframes blink {
  0%, 100% {
    opacity: 1;
  }

  50% {
    opacity: 0;
  }
}

.terminal-output {
  color: #fff;
  margin-bottom: 15px;
  line-height: 2;
}

.terminal-output p {
  margin: 0 0 1em;
}
